.main{
  padding: 1.375em 0 1.875em;
  margin-top: -1.375em;
  position: relative;
  z-index: 2;
  overflow: hidden;
  &__inner{
    padding-top: 1.875em;
    display: grid;
    grid-template-columns: 51.5625em calc(100% - 51.5625em);
    position: relative;
  }
  &__balls{
    position: absolute;
    top: 5em;
    right: 100%;
    margin-right: 2.875em;
    width: 29.375em;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__title{
    font-family: $font2;
    font-size: 5.875em;
    line-height: 80%;
    letter-spacing: 0.05em;
    color: $secondary;
  }
  &__text{
    margin-top: 1.125em;
    @include grid100gap(3.5625em);
  }
  &__subtitle{
    font-weight: 500;
    font-size: 1.875em;
    line-height: 142%;
    text-transform: uppercase;
    color: $gray;
    span{
      color: $primary;
    }
  }
  &__desc{
    font-weight: 300;
    font-size: 1.875em;
    line-height: 142%;
    max-width: 20.467em;
    span{
      color: $secondary;
    }
    b{
      font-weight: 700;
    }
  }
  &__slider{
    margin-left: -11.4375em;
    margin-top: -1.25em;
    width: calc(100% + 18.9375em);
  }
  .swiper{

    &-container{
      width: 100%;
    }
    &-slide{
      opacity: 0;
      .girl-card__name{
        transition: all .3s;
        transition-delay: .6s;
        opacity: 0;
      }
      &-active{
        opacity: 1;
        .girl-card__name{
          opacity: 1;
        }
      }
    }
  }
}

.info{
  padding: 3.75em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 9em / 4));
    gap: 3em;
    margin-top: -6em;
  }
}

.progress-bar{
  padding: .125em;
  height: 3.25em;
  border-radius: 9.375em;
  border: 1px solid $secondary;
  &__fill{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 .6875em;
    font-weight: 500;
    color: #fff;
    background-color: $secondary;
    border-radius: inherit;
    span{
      font-size: 1.125em;
    }
    @media screen and (min-width: 993px){
      height: 100% !important;
    }
  }
}

.subscribe{
  padding: 4.5em 0 5.5em;
  .main__balls{
    left: unset;
    right: 0;
    transform: translateX(60%);
    top: unset;
    z-index: -1;
    bottom: 9em;
  }
}

.subscribe-form{
  max-width: 61.25em;
  margin: 0 auto;
  padding: 1.5625em 2.875em 3.25em;
  width: 100%;
  border: 3px solid #FDEEC2;
  border-radius: .625em;
  &__top{
    @include grid100gap(.0625em);
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 1.75em;
    line-height: 142%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
  &__desc{
    font-weight: 300;
    font-size: 1em;
    line-height: 142%;
  }
  &__sums{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1.2fr;
    gap: 1.25em;
    margin-top: 1.5em;
  }
  &__sum{
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      z-index: 2;
      opacity: 0;
      &:checked+label{
        background-color: $secondary;
        border-color: $secondary;
        color: #fff;
      }
    }
    label{
      width: 100%;
      height: 2.375em;
      border: 1px solid #828282;
      @include center;
      text-transform: uppercase;
      color: #4F4F4F;
      span{
        font-size: 1.125em;
      }
    }
  }
  &__inputs{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5em;
  }
  &__check{
    margin-top: 1.875em;
    max-width: 40em;
  }
  &__btn{
    width: 100%;
    height: 2.875em;
    margin-top: 1.875em;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    margin: 0;
    padding: 1.625em 0 1.875em;
    &__inner{
      padding: 0;
      grid-template-columns: 100%;
    }
    &__col{
      position: relative;
    }
    &__balls{
      width: 22.5em;
      right: unset;
      left: -6em;
      top: 1.5em;
    }
    &__content{
      font-size: 2em;
    }
    &__title{
      font-size: 2.5em;
    }
    &__text{
      margin-top: .875em;
      gap: .875em;
    }
    &__subtitle{
      font-size: 1em;
    }
    &__desc{
      font-size: .875em;
      max-width: 100%;
    }
    &__slider{
      width: 30.625em;
      margin: -2em 0 0 auto;
    }
  }

  .info{
    padding: 2.5em 0 1.5em;
    overflow: hidden;
    &__inner{
      position: relative;
    }
    &__grid{
      grid-template-columns: 100%;
      margin: 0;
      gap: 2em;
    }
    @media screen and (min-width: 577px){
      font-size: 1.5em;
      .progress-bar{
        height: 100%;
      }
    }
  }

  .progress-bar{
    position: absolute;
    left: 1.625em;
    top: 50%;
    transform: translateY(-50%);
    width: 2.375em;
    height: calc(100% - 8em);
    padding: 0;
    &__fill{
      width: 100% !important;
      align-items: flex-end;
      justify-content: center;
      padding: 0 0 .8125em;
      span{
        font-size: .875em;
      }
    }
  }

  .subscribe{
    padding: 1.5em 0 3em;
    .main__balls{
      display: none;
    }
  }

  .subscribe-form{
    padding: 1.125em .875em 2.5em;
    &__top{
      gap: .5em;
    }
    &__title{
      font-size: 1em;
      line-height: 120%;
      letter-spacing: 0.05em;
      width: calc(100% + 1em);
      margin-left: -.5em;
    }
    &__desc{
      font-size: .875em;
    }
    &__sums{
      gap: 1.1875em 1em;
      grid-template-columns: 1fr 1fr;
    }
    &__sum{
      &:last-child{
        grid-column: 1/-1;
      }
    }
    &__inputs{
      gap: 1.1875em 1em;
    }
    &__check,
    &__btn{
      margin-top: 1.1875em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__content{
      font-size: 1em;
    }
    &__balls{
      width: 11.6875em;
    }
    &__slider{
      margin-left: 3.75em;
      width: 20.625em;
    }
  }
}
