.girl-card{
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__name{
    position: absolute;
    display: flex;
    align-items: center;
    height: 3.125em;
    left: 2.375em;
    bottom: 40%;
    padding: 0 1.5em;
    color: $secondary;
    font-family: $font2;
    background-color: #fff;
    span{
      font-size: 2.375em;
      letter-spacing: 0.05em;
    }
  }
}

.info-card{
  &__num{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-text{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 140%;
      color: #4F4F4F;
    }
    &-line{
      width: 1px;
      height: 5em;
      background: #828282;
    }
  }
  &__desc{
    margin: .625em auto 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 7.5em;
    &-top{
      display: flex;
      align-items: center;
    }
    &-num{
      font-size: 4.4375em;
      font-family: $font2;
      color: $secondary;
    }
    &-icon{
      width: 2.75em;
      height: 2.75em;
      @include center;
      margin-left: .125em;
      flex-shrink: 0;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  &__title{
    font-weight: 700;
    font-size: 1em;
    text-align: center;
    min-height: 2em;
  }
  &__text{
    margin-top: 2.125em;
    position: relative;
    img{
      position: absolute;
      left: 50%;
      top: -.125em;
      transform: translateX(-50%);
      z-index: -1;
      max-width: 100%;
    }
    span{
      font-size: .8125em;
      line-height: 138%;
    }
  }
}

@media screen and (max-width: 992px){
  .info-card{
    display: flex;
    align-items: center;
    &__num{
      padding-left: .75em;
      position: relative;
      flex-shrink: 0;
      margin-right: .125em;
      &-text{
        position: absolute;
        left: 0;
        bottom: 0;
      }
      &-line{
        height: 1px;
        width: 5em;
        margin: 0;
      }
    }
    &__desc{
      align-items: unset;
      margin-right: .375em;
      flex-shrink: 0;
      width: 5.375em;
      max-width: unset;
      &-num{
        font-size: 2.625em;
      }
      &-icon{
        width: 1.75em;
        height: 1.75em;
      }
    }
    &__title{
      text-align: left;
      font-size: .875em;
      word-break: break-word;
    }
    &__text{
      margin: 0;
      img{
        top: -.6875em;
        width: 13em;
        max-width: unset;
        left: 0;
        transform: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .girl-card{
    &__name{
      height: 2.1875em;
      left: .9375em;
      span{
        font-size: 1.1875em;
      }
    }
  }
}
