.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid #828282;
    padding: 0 2.33em;
    height: 3.166em;
    width: 100%;
    background-color: transparent;
    font-weight: 300;
    font-size: .75em;
    color: $black;
    &::placeholder {
      color: #828282;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-color: $primary;
        background-color: $primary;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 1.6875em;
    position: relative;
    min-height: 1.25em;
    font-size: 1em;
    line-height: 134%;
    cursor: pointer;
    display: block;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid #828282;
      width: 1.25em;
      height: 1.25em;
      left: 0;
      top: 0;
    }
  }
  a{
    position: relative;
    z-index: 2;
    color: $primary;
    text-decoration: underline;
  }
}


@media screen and (max-width: 576px){
  .check{
    label{
      font-size: .8125em;
      padding-left: 2.076em;
      &:before{
        font-size: 1.538em;
        width: 1em;
        height: 1em;
      }
    }
  }
}
