.logo{
  display: block;
  width: 8.375em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: .8125em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    margin-right: 4.125em;
  }
  &__btn{
    padding: 0 2.5625em;
    height: 2.5em;
    margin-left: auto;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    color: $gray;
    text-transform: uppercase;
    margin-right: 2.0625em;
    span{
      font-size: .875em;
      line-height: 142%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .625em 0;
    &__logo{
      width: 5.625em;
      margin: 0 1.1875em;
    }
    &._open{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-.4375em,.25em);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            width: 100%;
            transform: rotate(45deg) translate(-.4375em,-.25em);
          }
        }
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    padding: 5em 1em 2em;
    transition: all .3s;
    background-color: #fff;
    opacity: 0;
    pointer-events: none;
  }

  .nav{
    flex-direction: column;
    &__link{
      margin: 0 0 1.125em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    width: 2em;
    height: 1.125em;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    span{
      display: block;
      width: 100%;
      height: .125em;
      background: $primary;
      border-radius: .125em;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
        width: 1.375em;
      }
    }

  }
}
